import { ActionSheet, Cell } from "@taroify/core"
import { ArrowRight } from "@taroify/icons"
import { useMemo, useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicActionSheet() {
  const [open, setOpen] = useState(false)
  const actions = useMemo(() => [
    { name: "", value: "1" },
    { name: "", value: "2" },
    { name: "", value: "3" },
  ], [])
  return (
    <>
      <Cell clickable isLink title=""  onClick={() => setOpen(true)} />
      <ActionSheet actions={actions} open={open} onSelect={() => setOpen(false)} onClose={setOpen} />
    </>
  )
}

function ActionSheetWithCancel() {
  const [open, setOpen] = useState(false)
  const actions = useMemo(() => [
    { name: "", value: "1" },
    { name: "", value: "2" },
    { name: "", value: "3" },
  ], [])
  return (
    <>
      <Cell
        clickable
        isLink
        title=""
        onClick={() => setOpen(true)}
      />
      <ActionSheet
        cancelText=""
        actions={actions}
        open={open}
        onSelect={() => setOpen(false)}
        onCancel={() => setOpen(false)}
        onClose={setOpen}
      />
    </>
  )
}

function ActionSheetWithDescription() {
  const [open, setOpen] = useState(false)
  const actions = useMemo(() => [
    { name: "", value: "1", subname: "" },
    { name: "", value: "2" },
    { name: "", value: "3" },
  ], [])
  return (
    <>
      <Cell
        clickable
        title=""
        isLink
        onClick={() => setOpen(true)}
      />
      <ActionSheet
        description=""
        cancelText=""
        actions={actions}
        open={open}
        onSelect={() => setOpen(false)}
        onCancel={() => setOpen(false)}
        onClose={setOpen}
      />
    </>
  )
}

function ActionSheetWithStatuses() {
  const [open, setOpen] = useState(false)
  const actions = useMemo(() => [
    { name: "", value: "1", style: { color: "#ee0a24" } },
    { name: "", value: "2", disabled: true },
    { name: "", value: "3", loading: true },
  ], [])
  return (
    <>
      <Cell clickable title="" isLink onClick={() => setOpen(true)} />
      <ActionSheet
        actions={actions}
        cancelText=""
        open={open}
        onSelect={(value) => {
          console.log(value)
          setOpen(false)
        }}
        onCancel={() => setOpen(false)}
        onClose={setOpen}
      />
    </>
  )
}

export default function ActionSheetDemo() {
  return (
    <Page title="ActionSheet " className="action-sheet-demo">
      <Block variant="card" title="">
        <BasicActionSheet />
        <ActionSheetWithCancel />
        <ActionSheetWithDescription />
      </Block>
      <Block variant="card" title="">
        <ActionSheetWithStatuses />
      </Block>
    </Page>
  )
}
